___CSS_LOADER_EXPORT___.push([module.id, `html {
  font-family: "Yusei Magic", sans-serif;
  scroll-behavior: smooth;
}

h1 {
  text-align: center;
}

.content {
  min-height: 70vh;
  border: 1px solid black;
  display: grid;
  grid-template-columns: 20% 80%;
}

.sidebar-section {
  padding: 5px;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.todo-section {
  padding: 5px 30px;
}

dialog::backdrop {
  background-color: #ffffff80;
}

#dialog {
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: black;
  color: whitesmoke;
  gap: 20px;
  width: 500px;
  height: 300px;
  overflow-y: auto;
  scrollbar-color: #ffffff80 transparent;
  scrollbar-width: thin;
}

.close-icon {
  display: flex;
  justify-content: end;
  font-size: 30px;
  transition: transform 0.5s ease-in-out;
}

.close-button:hover {
  transform: scale(1.2);
  cursor: pointer;
}

form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-container {
  display: flex;
  flex-direction: column;
  font-size: 18px;
}

.input-container input {
  border-radius: 5px;
  padding: 5px;
}

form :nth-child(6) {
  display: grid;
}
.checklist-button {
  margin-top: 5px;
  width: 100px;
  justify-self: center;
}

.add-button-container {
  display: flex;
  justify-content: center;
  font-size: 24px;
  border-radius: 5px;
}

.include-button {
  padding: 10px;
  border-radius: 20px;
  width: 150px;
  background-color: whitesmoke;
  color: black;
  font-weight: bold;
}

.project-display {
  margin-top: 5px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.project-display input {
  grid-column: 1 / 3;
}

#cancel-project-div {
  margin-top: 5px;
  grid-column: 1/3;
}

li {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

.todo-lists {
  margin: 20px auto;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.card {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  color: whitesmoke;
  text-align: center;
  display: flex;
  flex-direction: column;
}
